@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../../fonts/open-sans-v18-latin_cyrillic-300.woff2") format("woff2"), url("../../fonts/open-sans-v18-latin_cyrillic-300.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
