:root {
  --white: #ffffff;
  --black: #000000;
  --blue: #283645;
  --dark-blue: #1d2631;
  --grey: #f2f2f2;
  --pink: #d22856;
  --light-grey: #e5e5e5;
  --green: #2aab6d;
  --green-hover: #1c9b5e;
  --shadow: rgba(0, 1, 1, 0.15);
  --cancel-button: rgba(40, 54, 69, 0.1);
  --white-with-opacity: rgba(255, 255, 255, 0.15);
  --button-white-with-opacity: rgba(255, 255, 255, 0.3);
  --background: rgba(0, 0, 0, 0.3);
  --pink-with-opacity: rgba(210, 40, 86, 0.3);
  --blue-with-opacity: rgba(40, 54, 69, 0.1);
  --extra-blue-with-opacity: rgba(40, 54, 69, 0.3);
  --light-blue: rgba(40, 54, 69, 0.6);
  --android: #a4c639;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
